body {
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    line-height: 17px;
    font-style: normal;



    padding: 0;
    margin: 0;

    overflow-x: hidden; }
a {
    text-decoration: none;
    color:#000 {} }


header {
    background: url(./img/Bg.png) no-repeat;

    background-size: cover;


    position: relative;

    width: 100%;
    height: 100vh; }





.header_navigation {
    height: 120px;

    align-items: center;


    & a {
        color:#fff {}
        text-transform: uppercase; }

    &__logo {
        width: 175px;
        height: 30px;

        display: flex; }


    &__menu {
        display: flex;
        justify-content: space-between;
        align-items: center; }

    &__menu a {
        text-transform: uppercase;

        margin: 0 10px; }

    &__login {
        border: 1px solid #ffffff;
        border-radius: 6px;

        padding: 10px 30px; }

    &__signupBlock {
        display: flex;
        justify-content: flex-end;
        align-items: center; }

    &__signupBlock a {

        & img {
            margin-right: 10px; } } }

.header_info {
    color:#ffff {}


    position: absolute;
    transform: translate(0,-50%);
    top: 50%;
    left: 0;
    right: 0;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    align-items: center;


    width: 27%;


    & h1, & p, {
        margin-bottom: 50px; }
    & h1 {
        line-height: 86px;
        font-size: 72px; }
    & p {
        font-size: 24px;
        font-weight: 600;
        line-height: 150%;
        text-align: center; }

    & a {
        background: #ffffff;
        padding: 20px 50px;
        border-radius: 6px;

        color:transparent {}
        text-transform: uppercase; } }


.under_header_info {
    height: 500px;
    display: flex;
    align-items: center;

    &__block1 {

        & h2 {
            font-weight: 600;
            font-size: 36px;
            line-height: 43px;

            margin-bottom: 30px; }
        & p {
            text-align: justify;
            margin-bottom: 20px; }
        & a {
            color: #ED4C5C;
            text-align: justify; } }

    &__block2 {
        display: flex;
        justify-content: center;
        & span {
            position: relative;

            & a {
                position: absolute;
                top: 40%;
                left: -30px; } } } }

.features_section {
    background-color: #FAFAFA;

    width: 100%;
    height: 660px;


    &__container {
        &__block {
            position: relative;
            &__span {
                position: relative;
                & span {
                    position: absolute;
                    top: 60%;
                    left: 0;
                    right: 0;
                    margin: 0 auto;
                    padding: 40px 40px;

                    width: 80%;

                    background-color: #ffffff;
                    box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.1);

                    border-radius: 8px;

                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    text-align: center;
                    & h3 {
                        font-size: 18px;
                        line-height: 22px;
                        font-weight: bold; }
                    & a {
                        color: #ED4C5C; }
                    & h3, &p {
                        margin-bottom: 20px; } } } } } }
.title {
    text-align: center;
    font-weight: 600;
    font-size: 36px;
    margin: 60px 0; }


.technology_section {
    height: 500px;


    &__block {
        position: relative;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        text-align: center;

        height: 225px;

        margin-top: 50px;
        & h3 {
            font-weight: bold;
            font-size: 18px;

            margin: 20px 0; }

        &__number {
            position: absolute;
            top: -50px; }
        &__icon {
            position: absolute;
            top: 0; } } }


.video_section {
    background-color: #ED4C5C;

    position: relative;

    height: 500px;

    display: flex;

    &__container {
        display: flex;
        justify-content: center;
        &__info {
            & img {
                margin-right: 15px; } }
        &__info h3 {

            font-weight: 600;
            font-size: 36px;
            line-height: 43px;
            color: #ffffff;
            margin-bottom: 30px; }
        &__info p {
            color: #ffffff;
            line-height: 140%;


            margin-bottom: 30px; }

        &__info, &__videos {
            display: flex;
            flex-direction: column;
            justify-content: center; }



        &__videos {

            align-items: center;
            & span {
                position: relative;
                width: 445px;
                height: 310px;
                & img {
                    position: absolute;
                    border-radius: 15px; } } } } }

.blured_img {
    &_1, &_2 {
        opacity: .5;
        height: 270px!important;
        z-index: 0!important;
        top: 50%;
        transform: translateY(-50%); }
    &_1 {
        left: -8%; }
    &_2 {
        right: -8%; } }

.img_video_active {
    z-index: 5; }


.contact_section {
    height: 500px;

    display: flex;

    &__container {
        height: auto;

        display: flex;
        flex-direction: column;
        justify-content: center;
        &__block1, &__block2 {
            display: flex;
            flex-direction: column;
            align-items: flex-start; }

        &__block1 {
            margin-top: -60px;
            padding-right: 10%; }
        &__block2 {
            padding-right: 5%;
            & input,& select {
                border: 1px solid #333333;
                box-sizing: border-box;
                border-radius: 6px;
                padding: 20px 30px;
                margin: 0 30px 30px 0;

                width: 100%;

                text-overflow: ellipsis; }
            & button {
                text-transform: uppercase;
                color: #ffffff;
                font-weight: normal;
                font-size: 14px;
                line-height: 17px;
                padding: 15px 25px;

                background: #ED4C5C;
                border: 1.5px solid #ED4C5C;
                box-sizing: border-box;
                border-radius: 6px;

                align-self: flex-start; } } } }


input::placeholder, {
    color: rgba(19, 39, 67, 0.5);
    text-overflow: ellipsis; }



footer {
    background: #FAFAFA;

    height: 435px; }

.footer_block {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    margin-top: 85px;
    padding-right: 7%;

    font-weight: normal;
    font-size: 12px;
    line-height: 150%;

    & h4 {
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;

        margin-bottom: 35px; }

    & a {
        margin: 0 0 10px 0; }

    &__icons a {
        margin: 20px 0px 0 10px; } }

.footer_copyBlock p {
    font-size: 12px;
    line-height: 14px;

    color: #101010;

    margin: 60px 0 0 10px; }
